/* eslint-disable */
import axios from 'axios';

const urlBase = 'http://goat-server.com:4567/pac/api/v1/desafio';


export default {
    getAllComics: (callback) => {
        axios.get(urlBase).then((comics) => {
            if (callback) {
                callback(comics);
            }
            console.log(comics.data.data)
        })
    }
}


