<!-- eslint-disable --> 
<template>
    <md-card class="card-default">
      <md-card-header>
        <div class="md-title">
            REPUBLICA FEDERATIVA DO BRASIL
        </div>
      </md-card-header>

      <md-card-content>
        <img class="imagem-avatar" :src="imagem"/>
        <fieldset>
        <legend>Nome</legend>
        <span >{{nome}}</span>
        </fieldset>
        <h2>{{nome}}</h2>
        <p>{{documento}}</p>
        <p>{{cpf.numero}}</p>
        <p>{{categoria}}</p>
        <p>{{registrocnh}}</p>
        <p>{{validade}}</p>
        <p>{{primeiraHab}}</p>
        <p>{{filiacao}}</p>      
      </md-card-content>
    </md-card>
</template>
<script>
/* eslint-disable */
export default {
    name: 'registro',
    props: {
        imagem: {
            type: String
        },
        nome: {
            type: String,
            required: true
        },
        documento: {
            type: String
        },
        cpf: { 
            numero: {
                type: String    
            },
            type: String
        },
        categoria: {
            type: String
        },
        registrocnh: {
            type: String
        },
        validade: {
            type: Object
        },
        primeiraHab: {
            type: Object
        },
        filiacao: {
            type: Object
        }

    }
}
</script>

<style scoped>
    .imagem-avatar {
        width: 120px;
    }

    .card-default {
        padding: 28px;
        width: 600px;
        margin: 25px;
        justify-content: center;
        align-items: center;
        display: flex;
        flex-direction: column;
        background: #FFF !important;
        border: #000 solid;
    }
    fieldset {
    width: 400px;
    margin: 25px;
	border: 0.7px solid #999;
	border-radius:8px;
    }

    legend {
     display: table; 
     position: relative;
     font-size: 15px;
     text-align: left;
    }

    span {
        font-size:15px;
        text-align: right;
    }
</style>


