
<template>
  <div id="app" class="md-layout">
    <div class="md-layout-item md-size-33" v-for="registro in registros" :key="registro.id">
      <CNH
        :imagem="getImagem(registro)"
        :nome="registro.nome"
        :documento="registro.documento"
        :cpf="registro.cpf.numero"
        :categoria="registro.categoria"
        :registrocnh="registro.registrocnh"
        :validade="registro.validade"
        :primeiraHab ="registro.primeiraHab"
        :filiacao ="registro.filiacao"
      ></CNH>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import API from './services/API';
import CNH from './components/CNH';
export default {
  name: 'App',
  components: {
    CNH
  },
  data() {
    return {
     registros: []
    }
  },

  created() {
    var self = this
    API.getAllComics(comics => {
      self.registros = comics.data.data;
    })
  },

   methods: {
    getImagem: (registros) => {
        return registros.imageUrl;
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



